// src/stores/authStore.ts (  )
import { defineStore } from 'pinia'
import { ref, computed, readonly } from 'vue'
import axios from 'axios'
import { useToast } from 'vuestic-ui'

const AUTH_STORAGE_KEY = 'auth-store'

//   
interface User {
  employee_id: string | number
  employee_name: string
  department_code: string
  department_name?: string
  position?: string
  [key: string]: any
}

//    
interface DepartmentPermission {
  name: string
  modules: string[]
  description: string
}

export const useAuthStore = defineStore('auth', () => {
  // ================================
  //  
  // ================================
  const user = ref<User | null>(null)
  const token = ref<string>('')
  const isLoading = ref<boolean>(false)
  const isInitialized = ref<boolean>(false)
  
  // ================================
  //  
  // ================================
  const isLoggedIn = computed(() => !!user.value && !!token.value)
  
  const displayName = computed(() => {
    if (user.value) {
      return user.value.employee_name || ` ${user.value.employee_id}` || ''
    }
    return ''
  })
  
  const userRole = computed(() => {
    if (!user.value) return ''
    
    const parts: string[] = []
    if (user.value.position) parts.push(user.value.position)
    if (user.value.department_name || user.value.department_code) {
      parts.push(user.value.department_name || user.value.department_code)
    }
    
    return parts.join('  ')
  })

  // ================================
  //    
  // ================================
  
  //    (department  department_code )
  const DEPARTMENT_PERMISSIONS: Record<string, DepartmentPermission> = {
    '01': {
      name: '',
      modules: ['production'], //  
      description: '  '
    },
    '02': {
      name: '',
      modules: ['material', 'inventory'], // / 
      description: '  '
    },
    '03': {
      name: '',
      modules: ['packaging'], //   (  )
      description: '   '
    },
    '04': {
      name: '',
      modules: ['equipment'], //  
      description: '  '
    },
    '05': {
      name: '',
      modules: ['quality', 'inspection'], //  
      description: '  '
    },
    '06': {
      name: '',
      modules: ['logistics', 'shipping'], //  
      description: '  '
    },
    '07': {
      name: '',
      modules: ['sales', 'order'], //  
      description: '  '
    },
    '08': {
      name: '',
      modules: ['admin'], //  
      description: '  '
    },
  }
  
  //   
  const userDepartment = computed(() => {
    if (!user.value || !user.value.department_code) return null
    return DEPARTMENT_PERMISSIONS[user.value.department_code] || null
  })
  
  //     
  const hasModulePermission = (module: string): boolean => {
    if (!isLoggedIn.value) return false
    
    //    
    if (user.value?.position === '') return true
    
    if (!userDepartment.value) return false
    return userDepartment.value.modules.includes(module)
  }
  
  //    (Computed Properties)
  const isPackagingEmployee = computed(() => hasModulePermission('packaging'))
  //      /  
  const canManageLines = computed(() => 
    user.value?.position === '' || user.value?.department_code === '03'
  )
  const canViewLines = computed(() => 
    user.value?.position === '' || user.value?.department_code === '03'
  )
  const canManageProduction = computed(() => hasModulePermission('production'))
  const canManageMaterial = computed(() => hasModulePermission('material'))
  const canManageQuality = computed(() => hasModulePermission('quality'))
  const canManageLogistics = computed(() => hasModulePermission('logistics'))
  const canManageAdmin = computed(() => hasModulePermission('admin'))
  const canManageSales = computed(() => hasModulePermission('sales'))
  const canManageEquipment = computed(() => hasModulePermission('equipment'))
  
  //    ()
  const getPermissionMessage = (module: string = 'line_manage'): string => {
    if (!isLoggedIn.value) {
      return '    .'
    }
    
    //      
    if (user.value?.position === '') {
      return ''
    }
    
    //      
    if (module === 'line_manage' || module === 'line_view') {
      if (user.value?.department_code !== '03') {
        return '    .'
      }
      return ''
    }
    
    if (!userDepartment.value) {
      return '    .  .'
    }
    
    if (!hasModulePermission(module)) {
      const moduleNames: Record<string, string> = {
        'production': ' ',
        'material': ' ',
        'quality': ' ',
        'logistics': ' ',
        'admin': '',
        'sales': ' ',
        'packaging': ' ',
        'equipment': ' '
      }
      
      const moduleName = moduleNames[module] || module
      return `${moduleName}  . (: ${userDepartment.value.name} - ${userDepartment.value.description})`
    }
    
    return ''
  }
  
  //   
  const userPermissionSummary = computed(() => {
    if (!user.value) return null
    
    //     
    if (user.value.position === '') {
      return {
        departmentName: '',
        departmentCode: user.value.department_code,
        description: '   ',
        modules: ['admin', 'packaging', 'production', 'material', 'quality', 'logistics', 'sales', 'equipment'],
        permissions: {
          canViewLines: true,
          canManageLines: true,
          canManageProduction: true,
          canManageMaterial: true,
          canManageQuality: true,
          canManageLogistics: true,
          canManageAdmin: true,
          canManageSales: true,
          canManageEquipment: true
        }
      }
    }
    
    if (!userDepartment.value) return null
    
    return {
      departmentName: userDepartment.value.name,
      departmentCode: user.value?.department_code,
      description: userDepartment.value.description,
      modules: userDepartment.value.modules,
      permissions: {
        canViewLines: canViewLines.value,
        canManageLines: canManageLines.value,
        canManageProduction: canManageProduction.value,
        canManageMaterial: canManageMaterial.value,
        canManageQuality: canManageQuality.value,
        canManageLogistics: canManageLogistics.value,
        canManageAdmin: canManageAdmin.value,
        canManageSales: canManageSales.value,
        canManageEquipment: canManageEquipment.value
      }
    }
  })
  
  // ================================
  //   
  // ================================
  
  // axios   
  const setAxiosAuthHeader = (authToken: string) => {
    if (axios?.defaults?.headers?.common) {
      axios.defaults.headers.common['Authorization'] = `Bearer ${authToken}`
    }
  }
  
  // axios   
  const removeAxiosAuthHeader = () => {
    if (axios?.defaults?.headers?.common) {
      delete axios.defaults.headers.common['Authorization']
    }
  }
  
  //   
  const saveAuthData = (userData: User, userToken: string) => {
    const authData = {
      user: userData,
      token: userToken,
      timestamp: Date.now(),
      version: '1.0'
    }
    
    user.value = userData
    token.value = userToken
    
    // localStorage  (  )
    try {
      localStorage.setItem(AUTH_STORAGE_KEY, JSON.stringify(authData))
      sessionStorage.removeItem(AUTH_STORAGE_KEY) //  
    } catch (error) {
      console.error('localStorage  :', error)
    }
    
    // axios   
    setAxiosAuthHeader(userToken)
    
    console.log('  :', userData.employee_name || userData.employee_id)
    console.log(' :', userData.department_name || userData.department_code)
    console.log(' :', userData.department_code ? DEPARTMENT_PERMISSIONS[userData.department_code] || ' ' : ' ')
  }
  
  //   
  const loadAuthData = (): boolean => {
    try {
      const authDataStr = localStorage.getItem(AUTH_STORAGE_KEY) || 
                          sessionStorage.getItem(AUTH_STORAGE_KEY)
      
      if (!authDataStr) {
        console.log('   ')
        return false
      }
      
      const authData = JSON.parse(authDataStr)
      
      //   
      if (authData?.user && authData?.token) {
        //    (7) -    
        const sevenDaysInMs = 7 * 24 * 60 * 60 * 1000
        if (Date.now() - authData.timestamp > sevenDaysInMs) {
          console.log('  7   ')
        }
        
        user.value = authData.user
        token.value = authData.token
        
        setAxiosAuthHeader(authData.token)
        
        console.log('   :', authData.user.employee_name || authData.user.employee_id)
        console.log(' :', authData.user.department_name || authData.user.department_code)
        return true
      }
      
      console.log('   ')
      return false
    } catch (error) {
      console.error('   :', error)
      clearAuthData()
      return false
    }
  }
  
  //   
  const clearAuthData = () => {
    user.value = null
    token.value = ''
    
    try {
      localStorage.removeItem(AUTH_STORAGE_KEY)
      sessionStorage.removeItem(AUTH_STORAGE_KEY)
    } catch (error) {
      console.error('localStorage  :', error)
    }
    
    removeAxiosAuthHeader()
    
    console.log('  ')
  }
  
  //  
  const verifyToken = async (tokenToVerify?: string): Promise<boolean> => {
    try {
      const targetToken = tokenToVerify || token.value
      if (!targetToken) return false
      
      const response = await axios.get('http://localhost:3000/auth/verify', {
        headers: { Authorization: `Bearer ${targetToken}` },
        timeout: 10000
      })
      
      const isValid = response.data?.success === true
      console.log(isValid ? '  ' : '  ')
      return isValid
    } catch (error: any) {
      console.warn('  :', error.response?.status || error.message)
      return false
    }
  }
  
  // ================================
  //  
  // ================================
  
  //  ( )
  const login = async (employee_id: string, password: string) => {
    const { init: showToast } = useToast()
    
    try {
      isLoading.value = true
      
      console.log(' :', { employee_id })
      
      const response = await axios.post('http://localhost:3000/auth/login', {
        employee_id: employee_id.toString().trim(),
        password: password.toString().trim()
      }, {
        timeout: 15000,
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      })
      
      console.log(' :', response.data)
      
      if (response.data.success) {
        const { user: userData, token: userToken } = response.data
        
        //   
        saveAuthData(userData, userToken)
        
        // /   
        let welcomeMessage = `${userData.employee_name || userData.employee_id} !`

        
        showToast({
          message: welcomeMessage,
          color: 'success',
          duration: 4000
        })
        
        console.log(' :', userData.employee_name || userData.employee_id)
        console.log(' :', {
          department: userData.department_name || userData.department_code,
          position: userData.position,
          permissions: userData.position === '' ? 
            ' ' : 
            (userData.department_code ? DEPARTMENT_PERMISSIONS[userData.department_code] || ' ' : ' ')
        })
        
        return { success: true, user: userData }
      } else {
        throw new Error(response.data.message || ' .')
      }
      
    } catch (error: any) {
      console.error(' :', error)
      
      let errorMessage = ' .'
      
      if (error.response?.data?.message) {
        errorMessage = error.response.data.message
      } else if (error.response?.status === 401) {
        errorMessage = '    .'
      } else if (error.response?.status === 400) {
        errorMessage = ' .'
      } else if (error.response?.status === 429) {
        errorMessage = '   .    .'
      } else if (error.response?.status === 500) {
        errorMessage = '   .    .'
      } else if (error.code === 'ECONNABORTED') {
        errorMessage = '   .  .'
      } else if (error.request) {
        errorMessage = '   .  .'
      }
      
      showToast({
        message: errorMessage,
        color: 'danger',
        duration: 4000
      })
      
      return { success: false, message: errorMessage }
    } finally {
      isLoading.value = false
    }
  }
  
  // 
  const logout = async (routerInstance?: any) => {
    const { init: showToast } = useToast()
    
    try {
      isLoading.value = true
      
      console.log(' ...')
      
      //   
      if (token.value) {
        try {
          await axios.post('http://localhost:3000/auth/logout', {}, {
            headers: { Authorization: `Bearer ${token.value}` },
            timeout: 5000
          })
          console.log('   ')
        } catch (err) {
          console.warn('   :', err)
        }
      }
      
      clearAuthData()
      
      showToast({
        message: ' .',
        color: 'success',
        duration: 2000
      })
      
      console.log(' ')
      
      if (routerInstance) {
        setTimeout(() => {
          routerInstance.push({ name: 'dashboard' })
        }, 500)
      } else {
        setTimeout(() => {
          window.location.href = '/dashboard'
        }, 500)
      }
      
    } catch (error) {
      console.error(' :', error)
      
      clearAuthData()
      
      showToast({
        message: '    .',
        color: 'warning',
        duration: 3000
      })
      
      if (routerInstance) {
        setTimeout(() => {
          routerInstance.push({ name: 'dashboard' })
        }, 500)
      } else {
        setTimeout(() => {
          window.location.href = '/dashboard'
        }, 500)
      }
    } finally {
      isLoading.value = false
    }
  }
  
  //  ( )
  const initialize = async () => {
    if (isInitialized.value) {
      console.log(' ')
      return
    }
    
    try {
      console.log('AuthStore  ...')
      
      // axios  
      if (typeof axios !== 'undefined') {
        axios.defaults.baseURL = 'http://localhost:3000'
        axios.defaults.timeout = 15000
        axios.defaults.withCredentials = false
        axios.defaults.headers.common['Content-Type'] = 'application/json'
        axios.defaults.headers.common['Accept'] = 'application/json'
        axios.defaults.headers.post['Content-Type'] = 'application/json'
        
        console.log('axios   ')
        
        //   
        axios.interceptors.response.use(
          (response) => response,
          (error) => {
            console.error(' :', {
              status: error.response?.status,
              data: error.response?.data,
              message: error.message
            })
            
            if (error.response?.status === 401) {
              console.log('401     ')
            }
            return Promise.reject(error)
          }
        )
      }
      
      //    
      const hasAuth = loadAuthData()
      console.log(' -   :', hasAuth)
      
      if (hasAuth && token.value) {
        console.log('  , :', user.value?.employee_name || user.value?.employee_id)
        console.log(' :', {
          department: user.value?.department_name || user.value?.department_code,
          position: user.value?.position,
          permissions: user.value?.position === '' ? 
            ' ' : 
            (user.value?.department_code ? DEPARTMENT_PERMISSIONS[user.value.department_code] || ' ' : ' ')
        })
      } else {
        console.log('   -  ')
      }
      
      isInitialized.value = true
      console.log('AuthStore  ')
      
    } catch (error) {
      console.error('  :', error)
      isInitialized.value = true
      console.log('   -    ')
    }
  }
  
  // ================================
  //  
  // ================================
  const formatDate = (dateString: string): string => {
    if (!dateString) return '-'
    try {
      return new Date(dateString).toLocaleDateString('ko-KR')
    } catch (error) {
      return dateString
    }
  }
  
  //   
  const checkAuth = (): boolean => {
    return loadAuthData()
  }
  
  //  
  const forceLogout = () => {
    console.log('  ')
    clearAuthData()
    
    if (typeof window !== 'undefined') {
      window.location.href = '/login'
    }
  }
  
  //    ()
  const checkPermission = (module: string = 'line_manage', action: string = ' '): boolean => {
    if (!isLoggedIn.value) {
      console.log(`  :   - ${action}`)
      return false
    }
    
    //    
    if (user.value?.position === '') {
      console.log(`  :   - ${action}`)
      return true
    }
    
    //      
    if (module === 'line_manage' || module === 'line_view') {
      if (user.value?.department_code === '03') {
        console.log(`  :    - ${action}`)
        return true
      } else {
        console.log(`  :    - ${action}`)
        return false
      }
    }
    
    if (!hasModulePermission(module)) {
      console.log(`  : ${module}    - ${action}`)
      return false
    }
    
    console.log(`  : ${module}  - ${action}`)
    return true
  }
  
  // ================================
  //  
  // ================================
  return {
    //  (  )
    user: readonly(user),
    token: readonly(token),
    isLoading: readonly(isLoading),
    isInitialized: readonly(isInitialized),
    
    //  
    isLoggedIn,
    displayName,
    userRole,
    
    //   
    isPackagingEmployee,
    canManageLines,
    canViewLines,
    canManageProduction,
    canManageMaterial,
    canManageQuality,
    canManageLogistics,
    canManageAdmin,
    canManageSales,
    canManageEquipment,
    userDepartment,
    userPermissionSummary,
    hasModulePermission,
    getPermissionMessage,
    
    //  
    login,
    logout,
    initialize,
    
    //   
    verifyToken,
    loadAuthData,
    clearAuthData,
    checkAuth,
    forceLogout,
    
    //    
    checkPermission,
    
    //  
    formatDate,
  }
})

export default useAuthStore